import React from 'react';
import { useState } from 'react';
import './TinderCard.css';
import TinderCard from 'react-tinder-card';
import { useEffect } from 'react';
import database from './firebase';
import { usePromiseEffect } from './utils/promiseEffect';
import { getClient, getProvider } from './cards.provider';
import { AssignableCard } from './AssignableCard';

function TinderCards() {
    const [provider] = useState(getProvider('assignable'))

    const [data] = useState(provider.getCards())
    // piece of code which run based on condition

    console.log(data)
    return (
        <div>
            <div className="tinderCards__cardContainer">
                {data.map((person) => (
                    <TinderCard
                        className="swipe"
                        key={person.name}
                        preventSwipe={['up', 'down']}
                    >
                        <AssignableCard info={person}/>
                    </TinderCard>
                ))}
            </div>
        </div>
    )
}

export default TinderCards
